  font-size: 0;
  color: transparent;
  background: #fff;
  border-radius: 50%;
}
.slick-prev::before,
.slick-next::before {
  position: absolute;
  top: 10%;
  font-family: FontAwesome;
  font-size: 20px;
  color: #000;
}
.slick-prev {
  left: -25px;
}
.slick-prev:before {
  content: "\f053";
  left: 20%;
}
.slick-next {
  right: -25px;
}
.slick-next:before {
  content: "\f054";
  right: 20%;
}
/* Dots */
body {
