/*btn*/
.btn {
  display: inline-block;
  vertical-align: middle;
  height: 40px;
  line-height: 40px;
  line-height: 34px;
  padding: 0 35px;
  font-size: 24px;
  border-width: 2px;
  border-style: solid;
}
a.btn:hover,
input.btn:hover,
a.btn:focus {
  text-decoration: none;
}
a.btn-regular {
  display: block;
  margin: 0 auto;
  padding: 0;
  width: 190px;
  text-align: center;
  color: #fff;
  border-color: #fff;
  background: transparent;
}
a.btn-regular:hover {
  color: #000;
  border-color: #000;
  background: #fff;
}
input.btn-regular,
button.btn-regular {
  display: block;
  margin: 0 auto;
  padding: 0;
  width: 190px;
  text-align: center;
  color: #fff;
  border-color: #fff;
  background: transparent;
  cursor: pointer;
  transition: all .5s;
}
input.btn-regular:hover,
button.btn-regular:hover {
  color: #000;
  border-color: #000;
  background: #fff;
}
